import Vue from 'vue'
import Router from 'vue-router'
import PosMain from '@/components/posmain/PosMain'
import Sign from '@/components/sign/Sign'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/posmain',
      name: 'posmain',
      component: PosMain
    },
    {
      path: '/sign',
      name: 'sign',
      component: Sign
    },
    {
      path: '/',
      redirect: {
        name: 'sign'
      }
    }
  ]
})



