<template>
<div class="sign">
  <div class="box">
    <div class="logo">
      <img src="../../assets/images/logo.png">
      <p>高级点餐系统</p>
    </div>
    <!--<input type="text" placeholder="请输入手机号" class="input" @click="submit()" v-model="input">-->
    <el-input v-model="input" placeholder="请输入手机号" class="input"></el-input>
    <!--<button class="btn" @click="submit()">登录</button>-->
    <el-button type="primary" class="btn" @click="submit()">登录</el-button>
  </div>
</div>
</template>

<script>
import router from '../../router'
export default  {
  name: 'sign',
  data () {
    return {
      input: ''
    }
  },
  created () {
    if (localStorage.user_info){
      router.replace({path: 'posmain'})
    }
  },
  methods: {
    submit () {
      if (this.input === '') {
        this.$notify({
          title: '不能输入空的手机号',
          message: '友情提示',
          type: 'warning'
        })
      }else {
        this.$axios.post('/v1/users/infor',{
          mobile: this.input
        })
          .then(response => {
            console.log(response)
            if (response.data.code === 500){
              this.$alert('用户不存在！请联系管理员','Error!!',{
                confirmButtonText: '确定'
              })
              console.log(localStorage)
            } else {
              let users = {
                user_id: response.data.user_id,
                user_name: response.data.user_name
              }
              users = JSON.stringify(users)
              console.log(users)
              localStorage.setItem('user_info',users)
              router.push({path: 'posmain'})
            }
          })
          .catch(err => {
            console.log(err)
            this.$message({
              message: '网络错误',
              type: 'warning'
            })
          })
      }
    }
  }
}
</script>

<style lang="scss">
.sign {
  width:100%;
  height:100%;
  position: absolute;
  background: #f9fafc;
  .box {
    width:300px;
    height:400px;
    border-radius: 15px;
    border: 1px solid rgba(0,0,0,.3);
    margin:80px auto;
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: 1px 1px 8px rgba(20%,20%,40%,0.5);
    .logo {
      padding:5px 0;
      padding-bottom:0;
      img {
        width: 150px;
        height:42px;
      }
      p {
        padding:0;
        color: #5e7382;
      }
    }
    .input {
      width:70%;
      height:30px;
      margin: 10px auto;
    }
    .btn {
      /*width:40%;*/
      /*height:30px;*/
      margin:10px auto;
    }
  }
}
</style>



