<template>
  <div class="PosMain">
    <div class="top">
      <div class="top-left">
        <img src="../../assets/images/logo.png" class="logo">
        <span>点餐系统</span>
      </div>
      <div class="top-right">
        <div class="user">你好，老铁!!<span>{{userList.user_name}}</span></div>
      </div>
    </div>
    <el-row :span="24" style="width: 100%;"  v-loading.fullscreen.lock="loading">
      <el-col :span="7" class="postOrder" id="orderList">
        <el-tabs style="width: 100%;">
          <el-tab-pane label="点餐" style="width: 100%;" v-if="orderStart">
            <el-table :data="tableData" border style="width:100%" class="control">
              <el-table-column prop="shop" label="店家" style="width: 50%;">
              </el-table-column>
              <el-table-column prop="food" label="套餐" style="width: 50%;">
              </el-table-column>
            </el-table>
            <div class="divBtn">
              <el-button type="danger" @click="delAllGoods()">删除</el-button>
              <el-button type="success" @click="checkout()">确认</el-button>
            </div>
          </el-tab-pane>
          <div class="order-bingo" v-if="orderSuccess">
            <p class="order-title">点餐信息：</p>
            <p class="order-message" style="text-align: left;padding-left: 18px">{{userFood.shop}}: {{userFood.menu}}</p>
            <p class="order-message" style="text-align: left;padding-left: 18px">{{userFood.foods}}</p>
            <el-button type="primary" class="order-btn" @click="callOff()">取消订单</el-button>
          </div>
        </el-tabs>
      </el-col>
      <el-col :span="17">
        <div class="often-goods">
          <div class="title">已经有<span>{{foodOrderList.length}}</span>位同志点餐</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in foodOrderList">
                <span>{{goods.user_infor.user_name}}</span>
                <span class="o-price">{{goods.user_infor.department}}</span>
                <p>{{goods.restaurant_name}} {{goods.menu_infor.menu_name}}</p>
              </li>
            </ul>
          </div>


        </div>
        <div class="goods-type">
          <el-tabs>
            <el-tab-pane  v-for="shop in addressList" :label="shop.restaurant_name">
              <div>
                <ul class="cookList">
                  <el-button type="primary" v-for="(list,index) in shop.menu_list" @click="addOrderList(shop.restaurant_name,list.menu_name,list.menu_id)">
                     <span class="food-name">{{list.menu_name}}</span>
                  </el-button>
                </ul>
                <div class="bottom">
                  <figure v-for="(list,index) in shop.menu_list">套餐{{index+1}}&nbsp;&nbsp;{{list.description}}</figure>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
import router from '../../router'
export default {
  name: 'posmain',
  data() {
    return {
      tableData: [],
      oftenGoods:[],
      addressList:[],
      foodOrderList: [],
      userList: {},
      userFood: {},
      loading: true,
      orderStart: true,
      orderSuccess: false
    }
  },
  created () {
    let that = this;
//    获取登录信息
    if (localStorage.user_info) {
      let users = JSON.parse(localStorage.user_info)
      that.userList = users
      let id = users.user_id
      this.$axios.post('/v1/food_order/detail',{
        user_id: id
      })
        .then(response => {
         console.log(response.data)
          if (response.data.code === 500){
            this.$notify({
              title: '未点餐',
              message: '友情提示',
              type: 'warning'
            })
          } else {
            this.userFood = {
              foods: response.data.menu_infor.description,
              menu: response.data.menu_infor.menu_name,
              shop: response.data.restaurant_name
            }
            this.orderStart = false
            this.orderSuccess = true
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'warning'
          })
        })
//      console.log(users)
    } else {
      router.replace({path: '/sign'})
    }
    this.loading = true
//    订单人数请求
      that.$axios.get('/v1/food_order/list')
     .then(reponse=>{
       console.log('订单人数',reponse.data);
      that.foodOrderList = reponse.data
       that.loading = false
      // console.log(this.oftenGoods);
     })
     .catch(error=>{
      console.log(error);
      that.$message({
          message: '网络错误',
          type: 'warning'
        })
     })
    // 商家列表请求
      that.$axios.get('/v1/restaurants/list/')
     .then(reponse=>{
//      console.log(reponse.data)
       that.addressList = reponse.data
     })
     .catch(error=>{
      console.log(error);
       that.$message({
         message: '网络错误',
         type: 'warning'
       })
     })
  },
  mounted() {
    let orderHeight = document.body.clientHeight;
    document.getElementById('orderList').style.height = orderHeight + 'px';
  },
  updated(){
  },
  methods:{
     addOrderList(shopName,foodName,menuId){
        // 商品是否已经存在于订单列表中
        let that = this
        let newGoods = {
          shop : shopName,
          food : foodName,
          menuId : menuId
        }
        that.tableData.push(newGoods)
        if ( that.tableData.length > 1) {
          that.tableData.shift()
        }
     },
     delAllGoods(){
       this.tableData=[]
     },
     checkout(){
       if(this.tableData.length > 0 ){
         let menuId = this.tableData[0].menuId
         this.$axios.post('/v1/food_order/new',{
           user_id: this.userList.user_id,
           menu_id: menuId
         })
           .then(response => {
             console.log(response)
             if (response.data.code === 500) {
               this.tableData = []
               this.$notify({
                 title: response.data.msg,
                 message: '友情提示',
                 type: 'warning'
               })
               this.orderStart = false
               this.orderSuccess = true
             } else {
               this.tableData=[];
               this.$message({
                 message:'点餐成功！',
                 type:'success'
               })
               this.loading = true
               this.$axios.post('/v1/food_order/detail',{
                 user_id: this.userList.user_id
               })
                 .then(response => {
                   this.loading = false
                   if (response.data.code === 500){
                     this.$notify({
                       title: '未点餐',
                       message: '友情提示',
                       type: 'warning'
                     })
                   } else {
                     this.userFood = {
                       foods: response.data.menu_infor.description,
                       menu: response.data.menu_infor.menu_name,
                       shop: response.data.restaurant_name
                     }
                     this.orderStart = false
                     this.orderSuccess = true
                   }
                 })
                 .catch(err => {
                   console.log(err)
                   this.$message({
                     message: '网络错误',
                     type: 'warning'
                   })
                 })
               this.orderStart = false
               this.orderSuccess = true
             }
           })
           .catch(err => {
             console.log(err)
             this.$message({
               message: '网络错误',
               type: 'warning'
             })
           })
       }else {
         this.$message({
           message:'小哥，你得选一个呀',
           type: 'warning'
         })
       }
     },
     callOff () {
       this.$axios.post('/v1/food_order/cancel',{
         user_id: this.userList.user_id
       })
         .then(response => {
           console.log(response)
           this.$notify({
             title: response.data.msg,
             message: '友情提示',
             type: 'success'
           })
             this.orderStart = true
             this.orderSuccess = false
         })
         .catch(err => {
           console.log(err)
           this.$message({
             message: '网络错误',
             type: 'warning'
           })
         })
     }
  }
}
</script>


<style scoped lang="scss">
.PosMain {
  .top {
    width:100%;
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    background-color: #F9FAFC;
    border-bottom:1px solid #C0CCDA;
    .top-left {
      .logo {
        padding-left:8px;
      }
      span {
        padding-left:4px;
        font-size:14px;
        color: #8391a5;
      }
    }
    .top-right{
      color: #8391a5;
      padding:10px;
      span {
        color: #2c3e50;
      }
    }
  }
  .postOrder {
    background-color: #F9FAFC;
    border-right: 1px solid #C0CCDA;
    .control {
      overflow-x: hidden;
    }
    .divBtn {
      margin-top: 10px;
    }
    .order-bingo {
      display: flex;
      flex-direction: column;
      p{
        width:100%;
        color: #5e7382;
        margin:5px 0;
      }
      .order-title {
        padding-left: 10px;
        text-align: left;
      }
      .order-message {
        font-size: 18px;
        color: #1c8de0;
      }
    }
    .order-btn {
      width:30%;
      margin: 10px auto;
    }
  }
  .often-goods {
    .title {
      height: 20px;
      border-bottom: 1px solid #d3dce6;
      background-color: #F9FAFC;
      padding: 10px;
      text-align: left;
      span {
        padding:0 5px;
        color: #4db3ff;
      }
    }
    .often-goods-list {
      ul {
        max-height:150px;
        overflow-y: auto;
        li {
          float: left;
          border: 1px solid #E5E9F2;
          padding: 5px;
          margin: 10px;
          background-color: #ffffff;
          cursor: pointer;
          font-size: 13px;
          p {
            margin: 6px 0;
          }
        }
      }
      ul::-webkit-scrollbar-track
      {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        background-color: #F5F5F5;
      }
      ul::-webkit-scrollbar
      {
        width: 10px;
        background-color: #F5F5F5;
      }
      ul::-webkit-scrollbar-thumb
      {
        background-color: #0ae;

        background-image: -webkit-gradient(linear, 0 0, 0 100%,
          color-stop(.5, rgba(255, 255, 255, .2)),
          color-stop(.5, transparent), to(transparent));
      }
      .o-price {
        color: #58B7FF;
      }
    }
  }
  .goods-type {
    width:100%;
    clear: both;
    .cookList {
      width:90%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      .el-button {
        margin:8px; 
      }
    }
    .bottom {
      height:900px;
      overflow-y:auto;
      figure {
        text-align: left;
        padding:10px;
        color: #5e7382;
        border-radius:5px;
      }
    }
    .bottom::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
      background-color: #F5F5F5;
    }
    .bottom::-webkit-scrollbar
      {
        width: 10px;
        background-color: #F5F5F5;
      }
     .bottom::-webkit-scrollbar-thumb
      {
        background-color: #0ae;

        background-image: -webkit-gradient(linear, 0 0, 0 100%,
          color-stop(.5, rgba(255, 255, 255, .2)),
          color-stop(.5, transparent), to(transparent));
      } 
  }
}
</style>



